.App {
  background-color: #282c34;
  text-align: center;
  min-height: 100vh;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.App-header {
  min-height: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
}

h2, h1 {
  margin: 5px;
}

h2 {
  font-size: 0.75rem;
}